import { message } from 'antd'
import React, { useState } from 'react'
import { Button, Card, Col, Container, Form, Row } from 'react-bootstrap'

const Contact = () => {
  const [validatedContactUs, setValidatedContactUs] = useState(false)

  const [twitterLink, setTwitterLink] = useState('')
  const [instaLink, setInstaLink] = useState('')
  const [ytLink, setYTLink] = useState('')
  const [fbLink, setFbLink] = useState('')
  const [address, setAddress] = useState('')
  const [phone, setPhone] = useState('')
  const [email, setEmail] = useState('')

  const handleContactUsChange = (event) => {
    console.log('submtted')
    const form = event.currentTarget
    if (form.checkValidity() === false) {
      event.preventDefault()
      event.stopPropagation()
    }
    //make an api call for change
    message.success('Logo updated Successfully')
    setValidatedContactUs(true)
    event.preventDefault()
  }
  return (
    <>
      <Form
        noValidate
        validated={validatedContactUs}
        onSubmit={handleContactUsChange}
        autoComplete="off"
      >
        <Container className="border border-black rounded-4">
          <Row className="h3 ms-3 mt-2">Contact Page Settings</Row>
          <Card className="ms-3 my-3">
            <Card.Header>
              <b>Contact us Content Update</b>
            </Card.Header>
            <Card.Body>
              <Row className="mb-3">
                <div className="">
                  <Form.Group as={Col} className="col-12" controlId="formName">
                    <Form.Label>Address</Form.Label>
                    <Form.Control
                      as={'textarea'}
                      rows={3}
                      placeholder="Enter Address"
                      onChange={(e) => {
                        setAddress(e.target.value)
                      }}
                      value={address}
                      required
                    />
                  </Form.Group>
                </div>
              </Row>
              <Row className="mb-3">
                <Form.Group as={Col} className="col-12 col-md-6" controlId="email">
                  <Form.Label>Email</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Enter your Email Address"
                    onChange={(e) => {
                      setEmail(e.target.value)
                    }}
                    value={email}
                    required
                  />
                </Form.Group>

                <Form.Group as={Col} className="col-12 col-md-6" controlId="formName">
                  <Form.Label>Mobile number</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Enter your mobile number"
                    onChange={(e) => {
                      setPhone(e.target.value)
                    }}
                    value={phone}
                    required
                  />
                </Form.Group>
              </Row>
              <Row className="mb-3">
                <Form.Group as={Col} className="col-12 col-md-3 col-12" controlId="formName">
                  <Form.Label>Facebook Link</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Enter your facebook link"
                    onChange={(e) => {
                      setFbLink(e.target.value)
                    }}
                    value={fbLink}
                    required
                  />
                </Form.Group>

                <Form.Group as={Col} className="col-12 col-md-3 col-12" controlId="formName">
                  <Form.Label>Youtube Link</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Enter your Youtube link"
                    onChange={(e) => {
                      setYTLink(e.target.value)
                    }}
                    value={ytLink}
                    required
                  />
                </Form.Group>

                <Form.Group as={Col} className="col-12 col-md-3 col-12" controlId="formName">
                  <Form.Label>Instagram Link</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Enter your Instagram link"
                    onChange={(e) => {
                      setInstaLink(e.target.value)
                    }}
                    value={instaLink}
                    required
                  />
                </Form.Group>

                <Form.Group as={Col} className="col-12 col-md-3 col-12" controlId="formName">
                  <Form.Label>Twitter Link</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Enter your twitter link"
                    onChange={(e) => {
                      setTwitterLink(e.target.value)
                    }}
                    value={twitterLink}
                    required
                  />
                </Form.Group>
              </Row>
              <div className="d-flex justify-content-end">
                <div className="">
                  <Button>Save Changes</Button>
                </div>
              </div>
            </Card.Body>
          </Card>
        </Container>
      </Form>
    </>
  )
}

export default Contact
