import { message } from 'antd'
import React, { useState } from 'react'
import { Button, Card, Col, Container, Form, Row } from 'react-bootstrap'
const AboutUs = () => {
  const [validatedAbout, setValidatedAbout] = useState(false)
  const [introduction, setIntroduction] = useState('')
  const [aboutContent, setAboutContent] = useState('')
  const [selfPolicies, setSelfPolicies] = useState('')
  const handleAboutUsChange = (event) => {
    console.log('submtted')
    const form = event.currentTarget
    if (form.checkValidity() === false) {
      event.preventDefault()
      event.stopPropagation()
    }
    //make an api call for change
    message.success('Logo updated Successfully')
    setValidatedAbout(true)
    event.preventDefault()
  }
  return (
    <>
      <Form noValidate validated={validatedAbout} onSubmit={handleAboutUsChange} autoComplete="off">
        <Container className="border border-black rounded-4">
          <Row className="h3 ms-3 mt-2">About Us Page Settings</Row>
          <Card className="ms-3 my-3">
            <Card.Header>
              <b>About us Content Update</b>
            </Card.Header>
            <Card.Body>
              <Row className="mb-3">
                <div className="">
                  <Form.Group as={Col} className="col-12" controlId="formName">
                    <Form.Label>Introduction</Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Enter Introduction"
                      onChange={(e) => {
                        setIntroduction(e.target.value)
                      }}
                      value={introduction}
                      required
                    />
                  </Form.Group>
                </div>
              </Row>
              <Row className="mb-3">
                <div className="">
                  <Form.Group as={Col} controlId="formName">
                    <Form.Label>Content Section</Form.Label>
                    <Form.Control
                      as="textarea"
                      rows={3}
                      placeholder="Enter Content"
                      onChange={(e) => {
                        setAboutContent(e.target.value)
                      }}
                      value={aboutContent}
                      required
                    />
                  </Form.Group>
                </div>
              </Row>
              <Row className="mb-3">
                <div className="d-flex justify-content-center align-items-center align-items-md-end  justify-content-md-between   flex-column flex-md-row">
                  <Form.Group
                    as={Col}
                    className="col-sm-12 col-lg-8 col-xs-12 col-md-8 col-12"
                    controlId="formName"
                  >
                    <Form.Label>
                      Self Policies{' '}
                      <span className="text-danger">
                        *Note write your own business policy if required
                      </span>
                    </Form.Label>
                    <Form.Control
                      as={'textarea'}
                      rows={3}
                      placeholder="Enter your custom policies"
                      onChange={(e) => {
                        setSelfPolicies(e.target.value)
                      }}
                      value={selfPolicies}
                      required
                    />
                  </Form.Group>
                  <div className="mt-3 mt-md-0">
                    <Button type="submit">Update Changes</Button>
                  </div>
                </div>
              </Row>
            </Card.Body>
          </Card>
        </Container>
      </Form>
    </>
  )
}

export default AboutUs
