import React, { useState } from 'react'
import { Button, Card, Col, Container, Form, Row } from 'react-bootstrap'

const Home = () => {
  const [validatedHome, setValidatedHome] = useState(false)
  const [businessName, setBusinessName] = useState('')
  const [contentSection, setContentSection] = useState('')
  const [contactUsHeadingHome, setContactUsHeadingHome] = useState('')

  const handleHomeChange = (event) => {
    const form = event.currentTarget
    if (form.checkValidity() === false) {
      event.preventDefault()
      event.stopPropagation()
    }
    setValidatedHome(true)
    event.preventDefault()
  }

  return (
    <>
      <Form noValidate validated={validatedHome} onSubmit={handleHomeChange} autoComplete="off">
        <Container className="border border-black rounded-4">
          <Row className="h3 ms-3 mt-2">Home Settings</Row>
          <Card className="ms-3 mb-3">
            <Card.Header>
              <b>Home Top Section</b>
            </Card.Header>
            <Card.Body>
              <Row className="mb-3">
                <div className="">
                  <Form.Group
                    as={Col}
                    className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
                    controlId="formName"
                  >
                    <Form.Label><b>BusinessName</b></Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Enter Business Name"
                      onChange={(e) => {
                        setBusinessName(e.target.value)
                      }}
                      value={businessName}
                      required
                    />
                  </Form.Group>
                </div>
              </Row>
              <Row className="mb-3">
                <div className="">
                  <Form.Group as={Col} controlId="formName">
                    <Form.Label><b>Content Section</b></Form.Label>
                    <Form.Control
                      as="textarea"
                      rows={3}
                      placeholder="Enter Content"
                      onChange={(e) => {
                        setContentSection(e.target.value)
                      }}
                      value={contentSection}
                      required
                    />
                  </Form.Group>
                </div>
              </Row>
              <Row className="mb-3">
                <div className="d-flex justify-content-center align-items-center align-items-md-end  justify-content-md-between   flex-column flex-md-row">
                  <Form.Group
                    as={Col}
                    className="col-sm-12 col-lg-4 col-xs-12 col-md-4 col-12"
                    controlId="formName"
                  >
                    <Form.Label><b>Contact Us Heading </b></Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Enter Contact Us Heading"
                      onChange={(e) => {
                        setContactUsHeadingHome(e.target.value)
                      }}
                      value={contactUsHeadingHome}
                      required
                    />
                  </Form.Group>
                </div>
              </Row>

              <div className="d-flex justify-content-end">
                <div className="mt-3 mt-md-0">
                  <Button type="submit">Update Changes</Button>
                </div>
              </div>
            </Card.Body>
          </Card>
        </Container>
      </Form>
    </>
  )
}

export default Home
