import React, { useState } from 'react'
import '../../style.css'
import { Link } from 'react-router-dom'
import Home from './Home'
import Logo from './Logo'
import AboutUs from './AboutUs'
import Contact from './Contact'

const Index = () => {
  const [activeTab, setActiveTab] = useState('Home')

  const handleTabClick = (tab) => {
    setActiveTab(tab)
  }

  return (
    <div className="container">
      <ul className="nav nav-tabs ms-3">
        <li className="nav-item">
          <Link
            className={`nav-link myTab text-black ${activeTab === 'Home' ? 'active' : ''}`}
            onClick={() => handleTabClick('Home')}
            to="#"
          >
            Home
          </Link>
        </li>
        <li className="nav-item">
          <Link
            className={`nav-link myTab text-black ${activeTab === 'Logo' ? 'active' : ''}`}
            onClick={() => handleTabClick('Logo')}
            to="#"
          >
            Logo
          </Link>
        </li>
        <li className="nav-item">
          <Link
            className={`nav-link myTab text-black ${activeTab === 'About Us' ? 'active ' : ''}`}
            onClick={() => handleTabClick('About Us')}
            to="#"
          >
            About Us
          </Link>
        </li>
        <li className="nav-item">
          <Link
            className={`nav-link myTab text-black ${activeTab === 'Contact' ? 'active ' : ''}`}
            onClick={() => handleTabClick('Contact')}
            to="#"
          >
            Contact
          </Link>
        </li>
      </ul>
      <div className="tab-content">
        {activeTab === 'Home' && (
          <div className="tab-pane active" id="home-content">
            <Home />
          </div>
        )}
        {activeTab === 'Logo' && (
          <div className="tab-pane active mb-3" id="logo-content">
            <Logo />
          </div>
        )}
        {activeTab === 'About Us' && (
          <div className="tab-pane active mb-3" id="about-us-content">
            <AboutUs />
          </div>
        )}
        {activeTab === 'Contact' && (
          <div className="tab-pane active mb-3" id="contact-us-content">
            <Contact />
          </div>
        )}
      </div>
    </div>
  )
}

export default Index

