import { message } from 'antd'
import React, { useState } from 'react'
import { Button, Card, Col, Container, Form, Row } from 'react-bootstrap'

const Logo = () => {
  const [validatedLogo, setValidatedLogo] = useState(false)
  const [logo, setLogo] = useState('')
  const handleLogoChange = (event) => {
    console.log('logo', logo)
    console.log('submtted')
    const form = event.currentTarget
    if (form.checkValidity() === false) {
      event.preventDefault()
      event.stopPropagation()
    }
    if (logo === '') {
      message.error('Please select a logo')
      event.preventDefault()
      event.stopPropagation()
      return
    } else {
      //make an api call for change
      message.success('Logo updated Successfully')
    }
    setValidatedLogo(true)
    event.preventDefault()
  }
  const logoPlaceHolder = {
    width: '250px',
    height: '250px',
    objectFit: 'contain',
    transform: 'scale(0.7)',
  }
  return (
    <>
      <Form noValidate validated={validatedLogo} onSubmit={handleLogoChange} autoComplete="off">
        <Container className="border border-black rounded-4">
          <Row className="h3 ms-3 mt-2">Logo</Row>
          <Card className="ms-3 my-3">
            <Card.Header>
              <b>Set logo</b>
            </Card.Header>
            <Card.Body>
              <Row className="mb-3">
                <div className="d-flex justify-content-center align-items-center align-items-md-end  justify-content-md-between   flex-column flex-md-row">
                  <Form.Group as={Col} controlId="formName" className="col-6">
                    <Form.Label>Choose Logo</Form.Label>
                    <Form.Control
                      type="file"
                      onChange={(e) => {
                        setLogo(e.target.value)
                      }}
                      value={logo}
                      required
                    />
                  </Form.Group>
                  <div className="mt-3 mt-md-0">
                    <Button type="submit">Update Logo</Button>
                  </div>
                </div>
              </Row>
            </Card.Body>
          </Card>
          <Card className="ms-3 my-3">
            <Card.Header>Current Logo</Card.Header>
            <Card.Body>
              <div
                className={`${logoPlaceHolder}`}
                style={{
                  height: '200px',
                  width: '200px',
                  backgroundColor: '#0e1726',
                  objectFit: 'contain',
                }}
              >
                <div className=" w-100 h-100" style={{}}></div>
              </div>
            </Card.Body>
          </Card>
        </Container>
      </Form>
    </>
  )
}

export default Logo
